.form {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  animation: slideFromRight 600ms ease;
}

@keyframes slideFromRight {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0%);
  }
}

.the_chat {
  overflow-y: scroll;
  display: flex;
  flex-direction: column-reverse;
  width: 100%;
  height: 100%;
}

.empty {
  flex-basis: 5px;
}

.curr_message {
  position: relative;
  max-width: 75%;
  margin-bottom: 10px;
  margin-top: 10px;
  border-radius: 0.8em;
  min-width: 60px;
}

.horizontal {
  display: flex;
  flex-direction: row;
  width: calc(100% - 2px);
  height: 50px;
}

.input {
  border-width: 0;
  outline: none;
  flex-grow: 1;
  height: 100%;
  font-size: 20px;
  background-color: rgb(100, 101, 102);
  color: rgb(255, 255, 255);
  border-radius: 15px;
  padding: 5px;
}

.img {
  bottom: 5px;
  position: relative;
  width: 45px;
  height: 50px;
  object-fit: contain;
  margin: 5px 5px 0 5px;

  :hover:focus {
    filter: invert(20%);
  }
}

::placeholder {
  color: rgb(221, 221, 221);
  opacity: 0.8;
}
